<template>
  <!---->
  <el-dialog
    width="1300px"
    append-to-body
    v-if="dialogVisible"
    :title="title"
    :close-on-click-modal="false"
    :visible.sync="dialogVisible"
    @close="hideDialog"
  >
    <div class="dialog-body">
      <ul class="dialog-body-panel">
        <li
          v-for="item in btnList"
          :key="item"
          :class="['bmap-btn', `bmap-${item}`]"
          :style="{
            'background-position-y': activeBtn === item ? '-52px' : '0px',
          }"
          @click="handleDraw(item)"
        ></li>
      </ul>

      <div v-if="dialogVisible" id="map-container" ref="map" class="map"></div>
    </div>

    <div slot="footer" class="dialog-footer">
      <el-button @click="hideDialog"> </el-button>
      <el-button type="primary" @click="handleSubmit()"></el-button>
    </div>
  </el-dialog>
</template>

<script>
import { loadBaiDuDrawMap } from "./bmpgl_lib";

export default {
  name: "bdGlDrawAreaDialog",
  components: {},
  props: {
    ak: {
      required: true,
      type: String,
      default: "",
    },
    btnList: {
      type: Array,
      default: () => {
        return ["marker", "polyline", "rectangle", "polygon", "circle"];
      },
    }, // marker,polyline,rectangle,polygon,circle
    mapConfig: {
      type: Object,
      default: () => {
        return {
          lng: 113.16, // 
          lat: 40.9556,
          zoom: 12,
          heading: 10, // 
          tilt: 19, // 
          styleId: "", //  e6360ab59a74ddb51b584fb38d1cc191
          // mapType: BMAP_EARTH_MAP, // BMAP_EARTH_MAP BMAP_NORMAL_MAP 
        };
      },
    }, // 
    isCustomCenter: {
      type: Boolean,
      default: false,
    }, // 
    mapCenter: {
      type: Object,
      default: () => {
        return {
          lng: 113.16, // 
          lat: 40.9556,
        };
      },
    }, // isCustomCenterfalse
    styleOptions: {
      type: Object,
      default: () => {
        return {
          strokeColor: "#43a8ff", // 
          fillColor: "#43a8ff", // 
          strokeWeight: 3, // 
          strokeOpacity: 1, // 0-1
          fillOpacity: 0.2, // 0-1
        };
      },
    }, // 
    labelOptions: {
      type: Object,
      default: () => {
        return {
          borderRadius: "2px",
          background: "#FFFBCC",
          border: "1px solid #E1E1E1",
          color: "#703A04",
          fontSize: "12px",
          letterSpacing: "0",
          padding: "5px",
        };
      },
    }, // 
  },
  dicts: [],
  data() {
    return {
      title: "",
      map: null,
      BMapGL: null,
      dialogVisible: false,
      activeBtn: "", // 
      markerObj: {
        e: undefined,
        point: undefined,
        address: undefined,
      }, // 
      polylineObj: {
        e: undefined,
        pointsArr: undefined,
      }, // 
      rectangleObj: {
        e: undefined,
        pointsArr: undefined,
      }, // 
      polygonObj: {
        e: undefined,
        pointsArr: undefined,
      }, //  
      circleObj: {
        e: undefined,
        center: undefined,
        radius: undefined,
      }, // 
    };
  },
  methods: {
    /**
     * IP
     * @param:
     * @return: 
     * @author: mhf
     * @time: 2024-05-14 16:57:23
     **/
    getMyPosition() {
      return new Promise((resolve, reject) => {
        this.$nextTick(() => {
          const geolocation = new this.BMapGL.Geolocation();
          geolocation.getCurrentPosition((res) => {
            if (res && res.point) {
              resolve(res.point);
            } else {
              reject({
                lat: undefined,
                lng: undefined,
              });
            }
          });
        });
      });
    },

    /**
     * 
     * @author: mhf
     * @time: 2024-05-15 13:41:54
     **/
    initMap() {
      this.$nextTick(() => {
        loadBaiDuDrawMap(this.ak).then(() => {
          this.BMapGL = BMapGL;
          this.map = new this.BMapGL.Map("map-container");
          let latLng = this.getMyPosition();
          this.map.enableScrollWheelZoom();
          this.map.setHeading(this.mapConfig.heading);
          this.map.setTilt(this.mapConfig.tilt);
          this.map.setMapStyleV2({
            styleId: this.mapConfig.styleId,
          });

          // this.map.setMapType(this.mapConfig.mapType); // todo 

          function getCoordinates(isCustomCenter, latLng, mapConfig) {
            return {
              lng: isCustomCenter ? mapConfig.lng : latLng.lng,
              lat: isCustomCenter ? mapConfig.lat : latLng.lat,
            };
          }

          // 
          const coordinates = getCoordinates(
            !this.isCustomCenter,
            latLng,
            this.mapConfig
          );
          // 
          this.map.centerAndZoom(
            new this.BMapGL.Point(coordinates.lng, coordinates.lat),
            this.mapConfig.zoom
          );

          this.echoMapPolygon();
        });
      });
    },

    /**
     *    ...
     * @param: todo 
     * @return:
     * @author: mhf
     * @time: 2024-05-31 16:35:25
     **/
    echoMapPolygon() {
      /*  */
      if (this.polygonObj.pointsArr) {
        let pointsArr = JSON.parse(this.polygonObj.pointsArr);
        let pointGlArr = pointsArr.map(
          (item) => new BMapGL.Point(item.lng, item.lat)
        );
        var polygon = new BMapGL.Polygon(pointGlArr, this.styleOptions);
        this.map.addOverlay(polygon);
        this.map.setViewport(pointsArr);
      }
    },

    /**
     * 
     * @param: btnType
     * @return:
     * @author: mhf
     * @time: 2024-05-15 13:43:10
     **/
    handleDraw(btnType) {
      this.clearLayers();
      this.activeBtn = btnType;
      // 
      const drawingManager = new BMapGLLib.DrawingManager(this.map, {
        //isOpen: true,        // 
        enableCalculate: false, // 
        enableSorption: true, // 
        sorptiondistance: 20, // 
        circleOptions: this.styleOptions, // 
        polylineOptions: this.styleOptions, // 
        polygonOptions: this.styleOptions, // 
        rectangleOptions: this.styleOptions, // 
        labelOptions: this.labelOptions, // label
      });
      if (
        drawingManager._isOpen &&
        drawingManager.getDrawingMode() === this.activeBtn
      ) {
        drawingManager.close();
        this.activeBtn === "";
      } else {
        drawingManager.setDrawingMode(this.activeBtn);
        drawingManager.open();
      }

      /*  */
      if (this.activeBtn === "marker") {
        let markerNum = 0;
        this.map.addEventListener("click", (e) => {
          markerNum++;
          if (markerNum > 1) {
            return;
          }
          drawingManager.close();
          this.activeBtn === "";
        });
        drawingManager.addEventListener("overlaycomplete", (e) => {
          const marker = e.overlay;
          // 
          let point = marker.getPosition();
          // 
          const geocoder = new this.BMapGL.Geocoder();
          // 
          geocoder.getLocation(point, (result) => {
            this.markerObj = {
              e,
              point: point,
              address: result,
            };
            console.log(this.markerObj, "");
          });
        });
      } else if (this.activeBtn === "polyline") {
        /*  */
        drawingManager.addEventListener("overlaycomplete", (e) => {
          this.polylineObj = {
            e: e,
            pointsArr: e.overlay.points.map((p) => p.latLng),
          };
          console.log(this.polylineObj, "");
        });
      } else if (this.activeBtn === "rectangle") {
        /*  */
        // 
        drawingManager.addEventListener("overlaycomplete", (e) => {
          this.rectangleObj = {
            e,
            pointsArr: e.overlay.getPath(),
          };
          console.log(this.rectangleObj, "");
        });
      } else if (this.activeBtn === "polygon") {
        /*  */
        drawingManager.addEventListener("overlaycomplete", (e) => {
          this.polygonObj = {
            e,
            pointsArr: e.overlay.getPath(),
          };
          console.log(this.polygonObj, "");
        });
      } else if (this.activeBtn === "circle") {
        /*  */
        // 
        drawingManager.addEventListener("overlaycomplete", (e) => {
          const circle = e.overlay;
          this.circleObj = {
            e,
            center: circle.getCenter(), // 
            radius: circle.getRadius(), // 
          };
          console.log(this.circleObj, "");
        });
      }
    },

    /**
     * 
     * @author: mhf
     * @time: 2024-05-15 13:50:59
     **/
    clearLayers() {
      let overlaysArr = this.map.getOverlays();
      overlaysArr.forEach((layer) => {
        this.map.removeOverlay(layer);
      });
      this.markerObj = {
        e: undefined,
        point: undefined,
        address: undefined,
      }; // 
      this.polylineObj = {
        e: undefined,
        pointsArr: undefined,
      }; // 
      this.rectangleObj = {
        e: undefined,
        pointsArr: undefined,
      }; // 
      this.polygonObj = {
        e: undefined,
        pointsArr: undefined,
      }; //  
      this.circleObj = {
        e: undefined,
        center: undefined,
        radius: undefined,
      }; // 
    },

    showDialog(data) {
      this.title = data.title;
      this.dialogVisible = true;

      /* todo   */
      this.polygonObj.pointsArr = data.data;
      /* todo   */

      let that = this;
      this.$nextTick(() => {
        that.initMap();
      });
    },

    hideDialog() {
      this.activeBtn = "";
      this.map = null;
      this.dialogVisible = false;
    },

    async handleSubmit() {
      console.log("");
      let obj = this[`${this.activeBtn}Obj`]; // Obj
      if (!obj.e) {
        this.$message({
          type: "error",
          message: "!",
        });
        return;
      }
      console.log(obj, "obj");
      this.$emit("on-response", obj);
      this.hideDialog();
    },

    /**
     * 
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-05-15 18:42:06
     **/
    echoData(data) {},
  },
  created() {},
  mounted() {},
};
</script>

<style lang="scss" scoped>
