<template></template>

<script>
export default {
  name: "bdMapVGl",
  components: {},
  mixins: [],
  props: {
    bdMap: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  computed: {},
  watch: {},
  filters: {},
  data() {
    return {};
  },
  methods: {
    /**
     * ShapeLayer
     * : [ShapeLayer](https://mapv.baidu.com/gl/docs/ShapeLayer.html)
     *
     * @param {Object} [params={}] - ShapeLayer
     * @property {string} [params.color='rgba(0, 214, 236, 0.8)'] - 
     * @property {boolean} [params.enablePicked=true] - 
     * @property {number} [params.selectedIndex=-1] - 
     * @property {string} [params.selectedColor='#ee1111'] - 
     * @property {boolean} [params.autoSelect=true] - 
     * @property {Array} [params.data=[]] - 
     * @property {Function} [params.onclick=() => {}] - 
     *
     * @author: mhf
     * @time: 2024-06-29 14:54:48
     */
    initShapeLayer(params = {}) {
      console.log("initShapeLayer");
      let {
        color = "rgba(0, 214, 236, 0.8)", // 
        enablePicked = true, // 
        selectedIndex = -1, // 
        selectedColor = "#ee1111", // 
        autoSelect = true, // 
        data = [], // 
        onclick = () => {},
      } = params;

      var view = new mapvgl.View({ map: this.bdMap });
      var shapeLayer = new mapvgl.ShapeLayer(params);
      view.addLayer(shapeLayer);
      // shapeLayer.setData(jsonData); // jsonDatajson
      this.bdMap.setDefaultCursor("default"); // 
    },
  },
};
</script>
