<template>
  <div id="map-container" :style="mapStyle">
    <bdMapVGl ref="bdMapVGl" :bdMap="bdMap" />
  </div>
</template>

<script>
import { loadBaiDuMap } from "../utils/asynchronousLoading";
import { flattenArr } from "../utils";
import bdMapVGl from "./bdMapVGl.vue";

export default {
  name: "bdMap",
  components: { bdMapVGl },
  mixins: [],
  props: {
    ak: {
      required: true,
      type: String,
      default: "",
    },
    listenOnce: {
      type: Boolean,
      default: false,
    }, // false

    mapConfig: {
      type: Object,
      default: () => {
        return {
          center: {
            lng: 116.404,
            lat: 39.915,
          },
          zoom: 12,
          style: {
            custom: "", // styleId  styleJson
            styleId: undefined, // styleIDak
            styleJson: undefined, // styleJsonak
          }, // 
        };
      },
    },

    infoWindowStyle: {
      type: Object,
      default: () => {
        return {
          "--popBg": "#ffffff", // 
          "--imgDisplay": "block", //  (blocknone)
          "--closeBtnColor": "#cccccc", // 
          "--titleHeight": "auto", // 
        };
      },
    },
  },
  computed: {
    mapStyle() {
      return this.infoWindowStyle;
    },
  },
  watch: {},
  filters: {},
  data() {
    return {
      bdMap: null, // 
      BMapGL: null, // GL
      sectionObj: {
        duration: undefined, // 
        distance: undefined, // 
        pointsArr: [], // 
      }, // 
      rightMenu: undefined, // 
      viewport: {
        WS: undefined,
        WN: undefined,
        EN: undefined,
        ES: undefined,
        zoom: undefined,
      },
      roadCondition: [], // 
    };
  },
  methods: {
    /**
     * 
     * @author: mhf
     * @time: 2024-03-04 10:05:19
     **/
    initMap() {
      this.$nextTick(() => {
        loadBaiDuMap(this.ak).then(() => {
          console.log(BMapGL, "this.BMapGL");
          this.BMapGL = BMapGL;
          this.bdMap = new this.BMapGL.Map("map-container", {
            preserveDrawingBuffer: true,
          }); // Map
          this.bdMap.centerAndZoom(
            new this.BMapGL.Point(
              this.mapConfig.center.lng,
              this.mapConfig.center.lat
            ),
            this.mapConfig.zoom
          ); // ,
          this.bdMap.enableScrollWheelZoom(true); // 

          if (this.mapConfig.style?.custom) {
            const { custom, styleJson, styleId } = this.mapConfig.style;
            let config;
            if (custom === "styleJson") {
              config = { styleJson }; // styleJsonak
            } else if (custom === "styleId") {
              config = { styleId }; // styleIDak
            }
            this.$nextTick(() => {
              this.bdMap.setMapStyleV2(config);
            });
          }

          //
          const listen_tilesloaded = () => {
            this.$emit("map-loaded", {
              map: this.bdMap,
              BMapGL: this.BMapGL,
            });
            this.mapChange();
            if (this.listenOnce) {
              this.bdMap.removeEventListener("tilesloaded", listen_tilesloaded);
            }
          };
          this.bdMap.addEventListener("tilesloaded", listen_tilesloaded);
        });
      });
    },

    /**
     * 
     *   drawPolyline return-polyline 
     * @param: isShowMyRoad: 
     * @param: isShowRoadCondition: 
     * @param: startPoint: {lat: '', lng: ''} 
     * @param: endPoint: {lat: '', lng: ''} 
     * @param: customObj: 
     * @param: clearRoadSectionType: (""trafficroadCondition)
     * @return: sectionObj: (isShowMyRoadtrue)
     * @Demo:    this.showRoadCondition({
     *              isShowRoadCondition: true,
     *              isShowMyRoad: true,
     *              startPoint: {
     *                lng: 120.741951,
     *                lat: 30.742613,
     *              },
     *              endPoint: {
     *                lng: 120.741951,
     *                lat: 31.742613,
     *              },
     *          })
     * @author: mhf
     * @time: 2024-03-05 14:29:01
     **/
    showRoadCondition(params = {}) {
      // console.log(params, "ShowRoadCondition");
      let {
        isShowRoadCondition = true, // 
        isShowMyRoad = false, // 
        startPoint = {
          lng: undefined, // 120.741951
          lat: undefined, // 30.742613
        },
        endPoint = {
          lng: undefined,
          lat: undefined,
        },
        customObj = {},
        clearRoadSectionType = "",
        traffic_autoViewport = false, // 
        traffic_showTraffic = true, // 
        traffic_widthIcon = false, // (false)
        traffic_enableDragging = false, // (false)
      } = params;

      let pointsArr = [];
      let that = this;

      if (isShowRoadCondition) {
        if (isShowMyRoad) {
          // 
          const startMarker = new this.BMapGL.Point(
            startPoint.lng,
            startPoint.lat
          );
          const endMarker = new this.BMapGL.Point(endPoint.lng, endPoint.lat);

          var searchComplete = (results) => {
            if (transit.getStatus() == BMAP_STATUS_SUCCESS) {
              var plan = results.getPlan(0);
              pointsArr = flattenArr(plan._lines, "points"); // 
              this.sectionObj = {
                duration: plan.getDuration(true), // 
                distance: plan.getDistance(true), // 
                pointsArr, // 
                params, // 
                plan,
              };
              /* Marker */
              if (!traffic_widthIcon) {
                this.$nextTick(() => {
                  this.bdMap.removeOverlay(results._end.marker);
                  this.bdMap.removeOverlay(results._start.marker);
                });
              }
              /*  */
              that.drawPolyline({
                pointsArr,
                customObj: {
                  ...customObj,
                  pointsArr,
                  traffic_customType: "traffic_transparent", // 
                },
                stroke: {
                  strokeStyle: "solid",
                  strokeColor: "#fff",
                  strokeWeight: 9,
                  strokeOpacity: 0,
                },
                isRightDelete: false,
                isViewport: false,
              });
            }
          };
          var transit = new this.BMapGL.DrivingRouteLine(this.bdMap, {
            renderOptions: {
              map: this.bdMap,
              autoViewport: traffic_autoViewport,
              enableDragging: traffic_enableDragging,
              lineLayerStyle: {
                strokeTextureUrl: null,
                showTraffic: traffic_showTraffic,
                // lineLayerColor: {
                //     color: '#ffffff',
                //     opacity: 1,
                // }
              },
            },
            onSearchComplete: searchComplete,
            onPolylinesSet: () => {
              this.$emit("return-section", this.sectionObj); // 
            },
          });

          // this.bdMap.setViewport([startMarker, endMarker])
          transit.search(startMarker, endMarker);
          this.roadCondition.push(transit);
        } else {
          this.bdMap.setTrafficOff(); // 
          this.$nextTick(() => {
            this.bdMap.setTrafficOn(); //  1
          });
          // this.bdMap.addTileLayer(new this.BMapGL.TrafficLayer()); //  2
          // this.bdMap.setTrafficOff(); // 
        }
      } else {
        this.$nextTick(() => {
          this.clearRoadSection(clearRoadSectionType);
        });
      }
    },

    /**
     * @Event 
     * @description: base64
     * @author: mhf
     * @time: 2024-09-14 14:09:28
     **/
    screenshot() {
      return this.bdMap.getMapScreenshot();
    },

    /**
     *   
     * @param: type: ""trafficroadCondition
     * @return:
     * @author: mhf
     * @time: 2024-05-22 13:35:55
     **/
    clearRoadSection(type = "") {
      const { bdMap, roadCondition } = this;
      function shouldClear(layerType, requestedType) {
        return requestedType === layerType || requestedType === "";
      }
      if (shouldClear("traffic", type)) {
        bdMap.setTrafficOff();
      }
      if (shouldClear("roadCondition", type)) {
        roadCondition.forEach((condition) => condition.clearResults());
      }
    },

    /**
     * , this.drawMarker({obj: {lng: 116.404, lat: 39.119}})
     * @param: obj 
     * @param: myIcon ,
     * @param: customObj ,
     * @param: isReturn marker
     * @param: isShowInfo 
     * @param: isFloatShadow true
     * @param: isShowInfo  true  key key,customObjkey,
     * @param: isResetMakeIcon marker
     * @param: myChooseIcon isResetMakeIcontrue,
     * @param: offsetX: X 
     * @param: offsetY: Y 
     * @param: html: `<span style="line-height: 60px;margin-left: 10px;color: #ff6b00">${item.name}</span>`
     * @param: newZoom: 
     * @param: className: marker 
     * @param:  isLabelShow: true,
     *           labelText: "label",
     *           labelsetX: 10,
     *           labelsetY: -60,
     *           labelStyle: {
     *             color: "#fff", // 
     *             backgroundColor: "#0e335b", // rgba
     *             border: "none", // 
     *             borderRadius: "5px", // 
     *             padding: "5px 10px", // 
     *             fontSize: "10px", // 
     *             fontWeight: "bold", // 
     *           },
     * @return: isReturn === true ? marker : null
     * @author: mhf
     * @time: 2024-03-04 10:05:59
     **/
    drawMarker(params = {}) {
      // todo 
      console.log(this.bdMap, "this.bdMap.getCenterAndZoom()");
      // console.log(params, "DrawMarker");
      let {
        obj,
        myIcon,
        customObj,
        isReturn = false,
        isShowInfo = false,
        isFloatShadow = true,
        key,
        isResetMakeIcon = false,
        myChooseIcon,
        offsetX = 0,
        offsetY = 0,
        isLabelShow = false,
        labelsetX = 0,
        labelsetY = 0,
        labelText = undefined,
        labelStyle = undefined,
        html = undefined,
        resetCenter = true, // 
        newZoom = false, // newZoomfalse0(resetCentertrue)
        className = undefined,
      } = params;
      let point = new this.BMapGL.Point(obj.lng, obj.lat); // 
      let marker = new this.BMapGL.Marker(point, {
        icon: myIcon ? myIcon : null,
        enableDragging: false, // 
      }); // 
      marker.setZIndex(1); // 
      if (isLabelShow) {
        // 
        let label = new this.BMapGL.Label(labelText, {
          offset: new this.BMapGL.Size(labelsetX, labelsetY), // 
        });
        if (labelStyle) {
          label.setStyle(labelStyle);
        }
        marker.setLabel(label);
      }
      marker.customObj = customObj;
      this.bdMap.addOverlay(marker);
      const markerClick = (e) => {
        // console.log("", e, e.target.customObj);
        if (resetCenter) {
          /*  */
          let nowZoom = this.bdMap.getZoom();
          this.setMapCenterAndZoom({
            lng: e.target.customObj.longitude,
            lat: e.target.customObj.latitude,
            zoom: newZoom || nowZoom,
          });
        }
        if (isResetMakeIcon) {
          /* marker */
          let chooseMarker = new this.BMapGL.Marker(point, {
            icon: myChooseIcon ? myChooseIcon : null,
          }); // 
          chooseMarker.customObj = {
            ...customObj,
            isChoose: true, // 
          }; //  customObj keykey

          // this.bdMap.removeOverlay(marker); // marker
          this.removeOverlay({
            callback: (e) => e.customObj?.isChoose,
          }); // marker
          chooseMarker.setZIndex(2); // 
          this.bdMap.addOverlay(chooseMarker);

          chooseMarker.addEventListener("click", (e) => {
            /* marker */
            this.$emit("return-marker", e.target.customObj); // 
          });
        }
        this.$emit("return-marker", e.target.customObj); // 
      };
      marker.addEventListener("click", markerClick);

      if (isShowInfo) {
        marker.addEventListener("mouseover", (e) => {
          console.log(e, "e");
          this.isOpenInfoWindow({
            flag: true,
            html: html ? html : e.target.customObj[key],
            offsetX: offsetX,
            offsetY: offsetY,
            marker: point,
            isFloatShadow,
          });
        });

        marker.addEventListener("mouseout", () => {
          this.isOpenInfoWindow({ flag: false });
        });
        if (className) {
          /*   , markerClass,bridge */
          setTimeout(() => {
            let classArr = className.split(",");
            classArr.forEach((name) => marker.domElement.classList.add(name));
          }, 500);
          this.$forceUpdate();
          // marker.domElement.classList.add(className);
        }
      }
      return isReturn ? marker : null;
    },

    /**
     * 
     * @param: pointsArr [{lng: 120.123, lat: 30.123}, ...]
     * @param: customObj  { key: value, ... }
     * @param: stroke 
     * @param: isRightDelete 
     * @return:
     * @author: mhf
     * @time: 2024-03-06 11:14:28
     **/
    drawPolyline(params = {}) {
      // console.log(params, "drawPolyline");
      let {
        pointsArr = [],
        customObj = {},
        stroke = {
          strokeStyle: "solid",
          strokeColor: "#5298fe",
          strokeWeight: 9,
          strokeOpacity: 1,
          zIndex: 3,
        },
        myChooseStroke = {
          strokeStyle: "solid",
          strokeColor: "#FFFFFF",
          strokeWeight: 9,
          strokeOpacity: 1,
          zIndex: 4,
        },
        isRightDelete = false,
        isViewport = true, // 
        resetViewport = false, // 
        isResetPolyline = true, // 
      } = params;

      let pointsGlArr = pointsArr.map(
        (item) => new this.BMapGL.Point(item.lng, item.lat)
      );
      let polyline = new this.BMapGL.Polyline(pointsGlArr, stroke);
      polyline.setZIndex(stroke.zIndex || 3);
      polyline.customObj = customObj; // 
      this.bdMap.addOverlay(polyline); // 
      if (isViewport) {
        this.bdMap.setViewport(pointsArr); // 
      }

      const polylineClick = (e) => {
        if (resetViewport) {
          /*  */
          this.setViewport(pointsArr);
        }
        if (isResetPolyline) {
          /*  */
          let myChoosePolyline = new this.BMapGL.Polyline(
            pointsGlArr,
            myChooseStroke
          );
          myChoosePolyline.customObj = {
            ...customObj,
            isChoose: true, // 
          };
          this.removeOverlay({
            callback: (e) => e.customObj?.isChoose,
          });
          myChoosePolyline.setZIndex(myChooseStroke.zIndex || 4);
          this.bdMap.addOverlay(myChoosePolyline);
        }
        console.error(" - drawPolyline", e);
        this.$emit("return-polyline", e.target.customObj); // 
        // todo 
        // e.stopPropagation() || e.domEvent.stopPropagation(); //  zoom   
      };

      polyline.addEventListener("click", polylineClick);

      if (isRightDelete) {
        polyline.addEventListener("rightclick", (e) => {
          let txtMenuItem = [
            {
              text: "",
              callback: () => {
                /*  */
                this.bdMap.removeOverlay(polyline);
                /*  */
                this.bdMap.removeContextMenu(this.rightMenu);
                /*  */
                polyline.removeEventListener("rightclick");
              },
            },
          ];
          this.addRightMenu(txtMenuItem);
        });
      }
    },

    /**
     * 
     * @param: pointArr [{lng: 116.387112, lat: 39.920977}, ...]
     * @param: config: 
     * @param: customObj  { key: value, ... }
     * @return:
     * @author: mhf
     * @time: 2024-03-07 13:31:56
     **/
    drawPolygon(params = {}) {
      console.error(params, "DrawPolygon");
      let {
        pointArr = [],
        config = {
          zIndex: 10, // 
          fillOpacity: 0.5, // 
          strokeStyle: "solid", // dashedsoliddotted
          fillColor: "#5298fe", // 
          strokeColor: "#5298fe", // 
          strokeWeight: 6, // 
          strokeOpacity: 1, // 
        },
        myChooseConfig = {
          zIndex: 11, // 
          fillOpacity: 0.8, // 
          strokeStyle: "solid", // dashedsoliddotted
          fillColor: "#FF0000", // 
          strokeColor: "#FF0000", // 
          strokeWeight: 6, // 
          strokeOpacity: 1, // 
        },
        customObj,
        isRightDelete = false, // 
        isViewport = false, // 
        resetViewport = true, // 
        isResetPolygon = true, // 
      } = params;
      let pointGlArr = pointArr.map(
        (item) => new this.BMapGL.Point(item.lng, item.lat)
      );
      var polygon = new this.BMapGL.Polygon(pointGlArr, config);
      polygon.customObj = customObj;
      this.bdMap.addOverlay(polygon);
      if (isViewport) {
        this.bdMap.setViewport(pointArr);
      }

      const polygonClick = (e) => {
        console.error("", e);
        if (resetViewport) {
          /*  */
          this.setViewport(pointArr);
        }
        if (isResetPolygon) {
          /*  */
          let myChoosePolygon = new this.BMapGL.Polygon(
            pointGlArr,
            myChooseConfig
          );
          myChoosePolygon.customObj = {
            ...customObj,
            isChoose: true, // 
          };
          console.log(myChoosePolygon, "myChoosePolygon");
          this.removeOverlay({
            callback: (e) => e.customObj?.isChoose,
          });
          myChoosePolygon.setZIndex(myChooseConfig.zIndex || 11);
          this.bdMap.addOverlay(myChoosePolygon);
        }
        this.$emit("return-polygon", e.target.customObj);
      };

      polygon.addEventListener("click", polygonClick);
      if (isRightDelete) {
        polygon.addEventListener("rightclick", (e) => {
          let txtMenuItem = [
            {
              text: "",
              callback: () => {
                /*  */
                this.bdMap.removeOverlay(polygon);
                /*  */
                this.bdMap.removeContextMenu(this.rightMenu);
                /*  */
                polygon.removeEventListener("rightclick");
              },
            },
          ];
          this.addRightMenu(txtMenuItem);
        });
      }
    },

    /**
     * 
     * @param: point{lng: 120.4295, lat: 30.2091}
     * @param: radius
     * @param: config: 
     * @param: customObj  { key: value, ... }
     * @return:
     * @author: mhf
     * @time: 2024-03-07 13:31:56
     **/
    drawCircle(params = {}) {
      console.error(params, "drawCircle");
      let {
        point = { lng: undefined, lat: undefined },
        radius,
        config = {
          zIndex: 13, // 
          fillOpacity: 0.5, // 
          strokeStyle: "solid", // dashedsoliddotted
          fillColor: "#5298fe", // 
          strokeColor: "#5298fe", // 
          strokeWeight: 6, // 
          strokeOpacity: 1, // 
          enableEditing: false, // 
        },
        myChooseConfig = {
          zIndex: 14, // 
          fillOpacity: 0.5, // 
          strokeStyle: "solid", // dashedsoliddotted
          fillColor: "#FF0000", // 
          strokeColor: "#FF0000", // 
          strokeWeight: 6, // 
          strokeOpacity: 1, // 
          enableEditing: false, // 
        },
        customObj,
        isRightDelete = false, // 
        isViewport = false, // 
        resetViewport = true, // 
        isResetCircle = true, // 
      } = params;
      let pointGl = new this.BMapGL.Point(point.lng, point.lat);
      var circleGl = new this.BMapGL.Circle(pointGl, radius, config);
      circleGl.customObj = customObj;
      this.bdMap.addOverlay(circleGl);
      if (isViewport) {
        this.bdMap.setViewport(point);
      }

      const circleGlClick = (e) => {
        console.error("", e);
        if (resetViewport) {
          /*  */
          this.setMapCenterAndZoom({
            lng: point.lng,
            lat: point.lat,
          });
        }
        if (isResetCircle) {
          /*  */
          let myChooseCircle = new this.BMapGL.Circle(
            pointGl,
            radius,
            myChooseConfig
          );
          myChooseCircle.customObj = {
            ...customObj,
            isChoose: true, // 
          };
          console.log(myChooseCircle, "myChooseCircle");
          this.removeOverlay({
            callback: (e) => e.customObj?.isChoose,
          });
          myChooseCircle.setZIndex(myChooseConfig.zIndex || 14);
          this.bdMap.addOverlay(myChooseCircle);

          // circleGl.customObj = {
          //   ...customObj,
          //   isChoose: true, // 
          // }
          // this.removeOverlay({
          //   callback: (e) => e.customObj?.isChoose,
          // });
          // circleGl.setStrokeColor(myChooseConfig.strokeColor || config.strokeColor)
          // circleGl.setFillColor(myChooseConfig.fillColor || config.fillColor)
          // circleGl.setStrokeOpacity(myChooseConfig.strokeOpacity || config.strokeOpacity)
          // circleGl.setFillOpacity(myChooseConfig.fillOpacity || config.fillOpacity)
          // circleGl.setStrokeWeight(myChooseConfig.strokeWeight || config.strokeWeight)
          // circleGl.setStrokeStyle(myChooseConfig.strokeStyle || config.strokeStyle)
          // myChooseConfig.enableEditing ? circleGl.enableEditing() : circleGl.disableEditing();
        }
        this.$emit("return-circle", e.target.customObj);
      };

      circleGl.addEventListener("click", circleGlClick);
      if (isRightDelete) {
        circleGl.addEventListener("rightclick", (e) => {
          let txtMenuItem = [
            {
              text: "",
              callback: () => {
                /*  */
                this.bdMap.removeOverlay(circleGl);
                /*  */
                this.bdMap.removeContextMenu(this.rightMenu);
                /*  */
                circleGl.removeEventListener("rightclick");
              },
            },
          ];
          this.addRightMenu(txtMenuItem);
        });
      }
    },

    /**
     * 
     * https://lbsyun.baidu.com/index.php?title=jspopularGL/guide/CustomOverlay
     * @param: createDOM, point, customObj, customOverlayConfig, isShowInfo, infoWindowConfig
     * @demo: 
     * function createDOM(config) {
     *    const img = document.createElement('img');
     *    img.style.width = '300px';
     *    img.style.height = '171px';
     *    img.src = config.url;
     *    img.draggable = false;
     *    return img;
     * }
     * @Event: customOverlay.getProperties() // 
     * @Event: customOverlay.getPoint()  // 
     * @Event: customOverlay.setPoint(new this.BMapGL.Point(120, 30)) // 
     * @Event: customOverlay.hide() // show() 
     * @return: customOverlay
     * @author: mhf
     * @time: 2024-07-24 14:00:52
     **/
    drawCustomOverlay(params = {}) {
      let {
        createDOM = () => {
          return "dom";
        }, // ,demo
        point = {
          lng: undefined, // 112.18
          lat: undefined, // 41.15
        }, // 
        customObj, // properties
        customOverlayConfig = {
          offsetX: -10, // 
          offsetY: -10, // 
          MinZoom: 4, // 
          MaxZoom: 12, // 
          enableMassClear: true, // 
          enableDraggingMap: true, // 
        }, // CustomOverlay(createDom, options: Object) options
        isShowInfo = false, // 
        infoWindowConfig = {
          html: "",
          key: "",
          offsetX: 0, // 
          offsetY: 0,
          isFloatShadow: false,
        }, // (window)

        isResetCenter = true, // 
        isResetZoom = false, // 
        newZoom = 10, // isResetZoom true.
      } = params;
      // 
      const defaultOverlayConfig = {
        offsetX: 0, // 
        offsetY: 0, // 
        // MinZoom: 4, // 
        // MaxZoom: 12, // 
        enableMassClear: true, // 
        enableDraggingMap: true, // 
      }; // 
      const overlayConfig =
        Object.keys(customOverlayConfig).length > 0
          ? customOverlayConfig
          : defaultOverlayConfig;
      const customOverlay = new this.BMapGL.CustomOverlay(createDOM, {
        point: new this.BMapGL.Point(point.lng, point.lat),
        properties: {
          customObj, // 
        }, // createDom(config)
        ...overlayConfig, // 
      });
      this.bdMap.addOverlay(customOverlay);

      this.$emit("return-overlay-instantiated", customOverlay); // 

      const customOverlayClick = (e) => {
        console.error("", e.target.properties);
        if (isResetCenter) {
          /*  */
          this.setMapCenter({
            lng: e.target.point.lng,
            lat: e.target.point.lat,
          });
        }
        if (isResetZoom) {
          /*  */
          this.setMapZoom({ zoom: newZoom });
        }
        // if (isResetMakeIcon) {
        //   /* marker */
        //   let chooseMarker = new this.BMapGL.Marker(point, {
        //     icon: myChooseIcon ? myChooseIcon : null,
        //   }); // 
        //   chooseMarker.customObj = {
        //     ...customObj,
        //     isChoose: true, // 
        //   }; //  customObj keykey
        //
        //   // this.bdMap.removeOverlay(marker); // marker
        //   this.removeOverlay({
        //     callback: (e) => e.customObj?.isChoose,
        //   }); // marker
        //   chooseMarker.setZIndex(2); // 
        //   this.bdMap.addOverlay(chooseMarker);
        //
        //   chooseMarker.addEventListener("click", (e) => {
        //     /* marker */
        //     this.$emit("return-marker", e.target.customObj); // 
        //   });
        // }
        this.$emit("return-overlay", e.target); // 
      };
      // 
      customOverlay.addEventListener("click", customOverlayClick);
      if (isShowInfo) {
        // 
        customOverlay.addEventListener("mouseover", (e) => {
          let marker = new this.BMapGL.Point(point.lng, point.lat); // 
          this.isOpenInfoWindow({
            flag: true,
            html: infoWindowConfig.html
              ? infoWindowConfig.html
              : e.target.properties[infoWindowConfig.key],
            offsetX: infoWindowConfig.offsetX,
            offsetY: infoWindowConfig.offsetY,
            marker: marker,
            isFloatShadow: infoWindowConfig.isFloatShadow,
          });
        });
        // 
        customOverlay.addEventListener("mouseout", () => {
          this.isOpenInfoWindow({ flag: false });
        });
      }
    },

    /**
     * 
     * @param: flag: true:  false: 
     * @param: html: 
     * @param: offsetX: X 
     * @param: offsetY: Y 
     * @param: marker: ,new this.BMapGL.Point(lng, lat);
     * @param: isFloatShadow: 
     * @return:
     * @author: mhf
     * @time: 2024-03-04 15:43:55
     **/
    isOpenInfoWindow(params = {}) {
      let {
        flag,
        html,
        offsetX = 0,
        offsetY = 0,
        marker,
        isFloatShadow = true,
      } = params;
      if (flag) {
        var infoWindow = new this.BMapGL.InfoWindow(html, {
          offset: new this.BMapGL.Size(offsetX, offsetY), // 
        });
        if (!isFloatShadow)
          this.bdMap.getPanes().floatShadow.style.display = "none"; // 
        this.bdMap.openInfoWindow(infoWindow, marker);
      } else {
        this.bdMap.closeInfoWindow();
      }
    },

    /**
     * 
     * @param: url 
     * @param: width 
     * @param: height 
     * @param: offsetW 
     * @param: offsetH 
     * @return:
     * @author: mhf
     * @time: 2024-03-04 10:20:50
     **/
    getIcon(params = {}) {
      // console.log(params, "GetIcon");
      let {
        url = "/img/endPoint.png",
        width = 25,
        height = 40,
        offsetW = 0,
        offsetH = 0,
      } = params;
      return new this.BMapGL.Icon(url, new this.BMapGL.Size(width, height), {
        anchor: new this.BMapGL.Size(width / 2, height + 2), // []
        imageOffset: new this.BMapGL.Size(offsetW, offsetH), // 
      });
    },

    /**
     *  /
     * @param: keycustomObjkey
     * @param: valuecustomObjvalue
     * @param: callbacktrue
     * @return:
     * @author: mhf
     * @time: 2024-03-04 14:07:27
     * @demo1:   this.removeOverlay({
     *              callback: (e) => e.customObj?.type == type
     *            })
     * @demo2:  this.removeOverlay({
     *              key: "type",
     *              value: type
     *           })
     **/
    removeOverlay(params = {}) {
      console.log(params, "RemoveOverlay");
      let { key, value, callback = () => false } = params;
      let overlaysArr = this.bdMap.getOverlays();
      /*  */
      const overlaysToRemove = overlaysArr.filter((overlay) => {
        if (key) {
          return overlay.customObj[key] === value;
        } else {
          return callback(overlay);
        }
      });
      /*  */
      if (overlaysToRemove.length > 0) {
        overlaysToRemove.forEach((overlay) => {
          this.bdMap.removeOverlay(overlay);
        });
      }
    },

    /**
     * 
     * @param: params{lat: , lng: , zoom: ()}
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-04 16:22:17
     **/
    setMapCenterAndZoom(params = {}) {
      let nowZoom = this.bdMap.getZoom();
      let { lat, lng, zoom = nowZoom } = params;
      this.bdMap.centerAndZoom(new this.BMapGL.Point(lng, lat), zoom);
    },

    /**
     * 
     * @param: params{lat: , lng: }
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-04 16:22:17
     **/
    setMapCenter(params = {}) {
      let { lat, lng } = params;
      this.bdMap.setCenter(new this.BMapGL.Point(lng, lat));
    },

    /**
     * 
     * @param: params{zoom: }
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-04 16:22:17
     **/
    setMapZoom(params = {}) {
      let { zoom } = params;
      this.bdMap.setZoom(zoom);
    },

    /**
     * 
     * @param: pointsArr [{lng: 116.387112, lat: 39.920977}, ...]
     * @return:
     * @author: mhf
     * @time: 2024-03-07 14:48:53
     **/
    setViewport(pointsArr) {
      this.bdMap.setViewport(pointsArr);
    },

    /**
     * 
     * @param: txtMenuItem [{text: "", callback: () => {}}, ...]
     * @return:
     * @author: mhf
     * @time: 2024-03-07 15:34:10
     **/
    addRightMenu(txtMenuItem = []) {
      this.rightMenu = new this.BMapGL.ContextMenu();
      for (var i = 0; i < txtMenuItem.length; i++) {
        this.rightMenu.addItem(
          new this.BMapGL.MenuItem(
            txtMenuItem[i].text,
            txtMenuItem[i].callback,
            100
          )
        );
      }
      this.bdMap.addContextMenu(this.rightMenu);
    },

    /**
     * 
     * @param:
     * @return: 
     * @author: mhf
     * @time: 2024-04-24 16:55:21
     **/
    getMapViewport() {
      let bounds = this.bdMap.getBounds(); // 
      let zoom = this.bdMap.getZoom();
      let WS = bounds.getSouthWest();
      let EN = bounds.getNorthEast();
      return {
        WS, // ()
        WN: new this.BMapGL.Point(WS.lng, EN.lat), // ()
        EN, // ()
        ES: new this.BMapGL.Point(EN.lng, WS.lat), // ()
        zoom,
      };
    },

    /**
     * /
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-04-24 17:34:05
     **/
    mapChange() {
      this.bdMap.addEventListener("zoomend", (e) => {
        this.viewport = this.getMapViewport();
        this.$emit("map-change", this.viewport); // 
        // console.log(this.viewport, "");
      });

      this.bdMap.addEventListener("dragend", (e) => {
        this.viewport = this.getMapViewport();
        this.$emit("map-change", this.viewport); // 
        // console.log(this.viewport, "");
      });

      this.bdMap.addEventListener("click", (e) => {
        console.error("", e);
        this.bdMap.closeInfoWindow();
        this.$emit("map-click", e);
      });
    },

    initShapeLayer(params = {}) {
      this.$refs.bdMapVGl.initShapeLayer(params);
    },
  },
  created() {},
  mounted() {
    this.initMap();
  },
  beforeDestroy() {
    // 
    // this.bdMap.removeEventListener("tilesloaded", this.listen_tilesloaded);
    // this.bdMap.removeEventListener("zoomend", this.mapZoomEnd);
    // this.bdMap.removeEventListener("dragend", this.mapDragEnd);
    // this.bdMap.removeEventListener("click", this.mapClick);
  },
  destroyed() {
    // this.bdMap.removeEventListener('click', handleClick);
  },
};
</script>

