<!--
 * @Description:  
 * @Author: mhf
 * @Date: 2024/9/19 16:03
-->
<template>
  <el-dialog
    width="1300px"
    destroy-on-close
    append-to-body
    v-if="dialogVisible"
    :title="title"
    :visible.sync="dialogVisible"
    :before-close="hideDialog"
    :close-on-click-modal="false"
  >
    <div class="dialog-body">
      <div class="point-self">
        <div :class="isShowLineTip ? 'point-self-top' : 'point-self-hidden'">
          <i
            :class="
              isShowLineTip ? ['el-icon-close', 'top-icon'] : 'el-icon-more'
            "
            @click="isShowLineTip = !isShowLineTip"
          />
          <div v-if="isShowLineTip" class="top-title"></div>
        </div>
        <div v-if="isShowLineTip">
          <el-table
            v-if="isShowLineTip"
            :data="positionList"
            height="48vh"
            style="width: 300px"
          >
            <el-table-column label="#" type="index" />
            <el-table-column prop="lng" label="" show-overflow-tooltip>
              <template slot-scope="scope">
                {{ scope.row.lng.toFixed(6) }}
              </template>
            </el-table-column>
            <el-table-column prop="lat" label="" show-overflow-tooltip>
              <template slot-scope="scope">
                {{ scope.row.lat.toFixed(6) }}
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <div id="map-container"></div>

      <div class="dialog-body-tip" v-if="canEdit">
        <div v-if="isShowTip">
          
        </div>
        <i
          :class="isShowTip ? 'el-icon-close' : 'el-icon-more'"
          @click="isShowTip = !isShowTip"
        />
      </div>

      <div class="dialog-body-search">
        <el-input
          @input="searchPlace"
          @clear="searchPlace"
          clearable
          v-model="searchText"
          size="small"
          placeholder=""
        ></el-input>
      </div>
      <div class="dialog-body-lng">
        <el-input
          @input="searchCenter"
          v-model="lng"
          placeholder=""
          size="small"
          clearable
        ></el-input>
      </div>
      <div class="dialog-body-lat">
        <el-input
          @input="searchCenter"
          v-model="lat"
          placeholder=""
          size="small"
          clearable
        ></el-input>
      </div>
      <div class="dialog-body-option">
        <el-checkbox v-model="showRoads" @change="handleChange"
          ></el-checkbox
        >
      </div>

      <div>
        <div class="dialog-body-res" v-if="searchRes.length > 0">
          <div
            v-for="(item, index) in searchRes"
            :key="index"
            @click="chooseIt(item)"
          >
            <el-tooltip
              effect="light"
              :content="item.address + item.title"
              placement="right-end"
              overflow
            >
              <span> {{ item.address + item.title }} </span>
            </el-tooltip>
          </div>
        </div>
        <div
          class="dialog-body-none"
          v-else-if="searchRes.length === 0 && searchText"
        >
          <em> ... </em>
        </div>
      </div>
    </div>

    <div class="dialog-footer" v-if="canEdit">
      <el-button @click="hideDialog"> </el-button>
      <el-button type="primary" @click="submitPoints"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
// import { deviceAllList } from "@/api/systemSetting/deviceLayout";
// import { roadAllList } from "@/api/systemSetting/roadSectionConfiguration";

let img = {
  startPoint: require("./../assets/img/startPoint.png"),
  handlePoint: require("./../assets/img/handlePoint.png"),
};

export default {
  name: "bdMapDrawLinesDialog",
  components: {},
  props: {
    canEdit: {
      type: Boolean,
      default: true,
    },
    isMany: {
      type: Boolean,
      default: false,
    }, // 
  },
  dicts: [],
  watch: {
    pointsArr: {
      handler(val) {
        // console.log("", val);
        this.positionList = val;
      },
      deep: true,
    },
  },
  data() {
    return {
      dialogVisible: false,
      title: "",
      bdMap: null,
      mapCenter: {
        lng: 120.3083403138811,
        lat: 30.27631859319542,
      },
      mapZoom: 12,
      startPoint: {
        lng: 112.54965575904082,
        lat: 41.305581349868795,
      },
      endPoint: {
        lng: 112.5491902212557,
        lat: 41.3055680080383,
      },
      drivingTime: 0, //  
      drivingDistance: 0, //  
      pointsArr: [
        // {
        //   lat: 30.748406263284365,
        //   lng: 120.73957130336815
        // },
        // {
        //   lat: 30.748406263284365,
        //   lng: 120.73957130336815
        // },
        // {
        //   lat: 30.748406263284365,
        //   lng: 120.73957130336815
        // }
      ], //  []
      mapClickListener: null, // 
      drawIndex: 0, // 
      transit: null, // 
      searchText: undefined, // 
      searchPoint: {}, // 
      searchRes: [], // 
      polyline: null, // 
      node: new BMapGL.Icon(
        // '//mapopen-pub-jsapigl.bj.bcebos.com/demoimg/zhongheyiyuan.png',
        img.handlePoint,
        new BMapGL.Size(12, 12),
        { offset: new BMapGL.Size(5, 5) }
      ), // 
      isShowTip: true, // 
      isShowLineTip: false, //
      positionList: [],
      isInsert: true,
      lng: "",
      lat: "",
      centerTimer: null,
      showRoads: true, // 
      allRoads: [], // 
      img: {
        startPoint: require("./../assets/img/startPoint.png"),
        endPoint: require("./../assets/img/endPoint.png"),
        handlePoint: require("./../assets/img/handlePoint.png"),
        Icon_road_blue_arrow: require("./../assets/img/Icon_road_blue_arrow.png"),
      },
    };
  },
  methods: {
    showDialog(data) {
      this.dialogVisible = true;
      this.title = data.title;
      this.drawIndex = 0;
      if (data.data) {
        this.isInsert = false;
        let pointsArr = JSON.parse(data.data) || [];
        this.pointsArr = this.isMany ? this.flat(pointsArr) : pointsArr;
        this.startPoint = this.pointsArr[0];
        this.endPoint = this.pointsArr.at(-1);
      } else {
      }
      this.initBdMap(data.data);
      this.getAllRoads();
    },

    hideDialog() {
      this.bdMap.removeEventListener("click", () => {});
      this.mapClickListener = null;
      this.isInsert = true;
      this.bdMap.destroy();
      this.bdMap = null;
      this.lng = "";
      this.lat = "";
      this.dialogVisible = false;
      this.removeSearch();
    },

    removeSearch() {
      // if (this.searchPoint) {
      //   this.removeMarker("searchPoint")
      // }
      this.searchPoint = {};
      this.searchText = undefined;
      this.searchRes = [];
    },

    submitPoints() {
      if (this.pointsArr.length < 2) {
        this.$message.warning("");
        return;
      }
      this.pointsArr = [];
      this.pointsArr = this.polyline.getPath(); // 
      // console.log({
      //   pointsArr: this.pointsArr,
      //   km: this.drivingDistance
      // })
      this.$emit("on-response", {
        pointsArr: this.pointsArr,
        km: this.drivingDistance,
      });
      this.hideDialog();
    },
    drawDeviceMarker(points) {
      points.forEach((ele) => {
        let point = {
          lng: ele.longitude,
          lat: ele.latitude,
        };
        this.makePoint(
          point,
          null,
          { name: "deviceName", ...ele },
          false,
          false,
          true,
          null,
          4,
          0,
          false
        );
      });
    },
    initBdMap(flag) {
      this.$nextTick(async () => {
        this.bdMap = new BMapGL.Map("map-container");
        // const res = await deviceAllList({ deviceType: 6 });
        // // console.log("res", res);
        // if (res.data.length > 0) {
        //   this.drawDeviceMarker(res.data);
        //   // this.mapCenter.lng = res.data[0].longitude;
        //   // this.mapCenter.lat = res.data[0].latitude;
        //   if (this.isInsert) {
        //     this.startPoint = {
        //       lng: res.data[0].longitude,
        //       lat: res.data[0].latitude,
        //     };
        //     this.endPoint = {
        //       lng: Number(res.data[0].longitude) + 0.0005,
        //       lat: Number(res.data[0].latitude),
        //     };
        //   }
        // }
        this.bdMap.centerAndZoom(
          new BMapGL.Point(this.mapCenter.lng, this.mapCenter.lat),
          this.mapZoom
        );
        this.bdMap.enableScrollWheelZoom(true);
        if (!flag) {
          // 
          this.handleDraw();
        } else {
          // 
          this.echoLine(flag);
        }
        setTimeout(() => {
          // this.mapResetView([this.startPoint, this.endPoint]);
        }, 500);
      });
    },

    /**
     * @Event 
     * @param: portArr  [pot1, pot2, pot3]
     * @description:
     * @author: mhf
     * @time: 2024-02-01 10:12:24
     **/
    mapResetView(portArr) {
      this.bdMap.setViewport(portArr);
    },

    /**
     * @Event 
     * @description: 15.9 -> 15.9; 500 -> 0.5
     * @author: mhf
     * @time: 2024-02-01 10:43:07
     **/
    convertDistance(distance) {
      let resKm = 0;
      if (distance.indexOf("") > -1) {
        resKm = distance.substring(0, distance.indexOf(""));
      } else if (distance.indexOf("") > -1) {
        resKm = distance.substring(0, distance.indexOf("")) / 1000;
      } else {
        resKm = distance;
      }
      return resKm;
    },

    /**
     * @Event 
     * @param: obj ,
     * @param: myIcon ,
     * @param: customObj ,
     * @param: isDrag 
     * @param: isReturn marker
     * @description: this.makePoint({lng: 116.404, lat: 39.119})
     * @author: mhf
     * @time: 2024-02-01 13:43:13
     **/
    makePoint(
      obj,
      myIcon,
      customObj,
      isDrag = false,
      isReturn = false,
      isShowInfo = false,
      html = null,
      offsetX = 0,
      offsetY = 0,
      isFloatShadow = true
    ) {
      let point = new BMapGL.Point(obj.lng, obj.lat); // 
      let marker = new BMapGL.Marker(point, {
        icon: myIcon ? myIcon : null,
        enableDragging: isDrag, // 
      }); // 
      marker.customObj = customObj;
      this.bdMap.addOverlay(marker);

      // 
      if (isDrag) {
        marker.addEventListener("dragend", (e) => {
          // const newPosition = e.latLng; // 
          console.log("", e);
        });
      }

      // marker.addEventListener("click", (e) => {
      //   console.log("", e);
      // });
      if (isShowInfo) {
        marker.addEventListener("click", (e) => {
          console.log(e, "e");
          this.isOpenInfoWindow({
            flag: true,
            html: html ? html : e.target.customObj[e.target.customObj.name],
            offsetX: offsetX,
            offsetY: offsetY,
            marker: point,
            isFloatShadow,
          });
        });
      }
      return isReturn ? marker : null;
    },
    /**
     * -mapComp
     * @param: flag: true:  false: 
     * @param: html: 
     * @param: offsetX: X 
     * @param: offsetY: Y 
     * @param: marker: ,new BMapGL.Point(lng, lat);
     * @param: isFloatShadow: 
     * @return:
     * @author: xxb
     * @time: 2024-06-14 15:43:55
     **/
    isOpenInfoWindow(params = {}) {
      let {
        flag,
        html,
        offsetX = 0,
        offsetY = 0,
        marker,
        isFloatShadow = true,
      } = params;
      if (flag) {
        var infoWindow = new BMapGL.InfoWindow(html, {
          offset: new BMapGL.Size(offsetX, offsetY), // 
        });
        if (!isFloatShadow)
          this.bdMap.getPanes().floatShadow.style.display = "none"; // 
        this.bdMap.openInfoWindow(infoWindow, marker);
      } else {
        this.$nextTick(() => {
          this.bdMap.closeInfoWindow();
          if (this.drawIndex < 2) {
            // this.bdMap.removeEventListener("click", () => {
            //   console.log("");
            // });
            // console.log(this.mapClickListener);
            this.handleDraw();
          }
        });
      }
    },

    /**
     * @Event 
     * @description:
     * @author: mhf
     * @time: 2024-02-21 11:25:52
     **/
    echoLine(receivedP) {
      let config = {
        strokeStyle: "dashed",
        strokeColor: "blue",
        strokeTexture: {
          // url: "https://mapopen-pub-jsapigl.bj.bcebos.com/svgmodel/Icon_road_blue_arrow.png",
          url: this.img.Icon_road_blue_arrow,
          width: 16,
          height: 64,
        },
        strokeWeight: 10,
        strokeOpacity: 0.8,
        node: this.node,
        zIndex: 100,
      };
      if (!this.isMany) {
        let pointsGlArr = []; // new BMapGL.Point
        const getPointGlArr = () => {
          return this.pointsArr.map(
            (item) => new BMapGL.Point(item.lng, item.lat)
          );
        };
        pointsGlArr = getPointGlArr();
        this.polyline = new BMapGL.Polyline(pointsGlArr, config);
        this.bdMap.addOverlay(this.polyline); // 
        this.polyline.enableEditing(); // 
        // this.polyline.disableEditing(); // 
        let pointsArr = this.polyline.getPath();
        this.makePoint(
          pointsArr[0],
          this.getIcon({
            index: 0,
            length: 0,
          }),
          { name: "handlePoint0" },
          false,
          false
        );
        this.makePoint(
          pointsArr[pointsArr.length - 1],
          this.getIcon({
            index: 1,
            length: 2,
          }),
          { name: "handlePoint1" },
          false,
          false
        );

        /*  */
        this.polyline.addEventListener("editend", (e) => {
          let pointsArr = this.polyline.getPath();
          // 
          this.removeMarker("handlePoint0");
          this.removeMarker("handlePoint1");
          this.makePoint(
            pointsArr[0],
            this.getIcon({
              index: 0,
              length: 0,
            }),
            { name: "handlePoint0" },
            false,
            false
          );
          this.makePoint(
            pointsArr[pointsArr.length - 1],
            this.getIcon({
              index: 1,
              length: 2,
            }),
            { name: "handlePoint1" },
            false,
            false
          );
        });
      }

      let polylineArr = JSON.parse(receivedP);
      polylineArr.forEach((item, index) => {
        // console.log(item, index)
        this.drawPolyLines(item, config);
      });
    },

    /**
     * @Event 
     * @description:
     * @author: mhf
     * @time: 2024-02-20 17:20:10
     **/
    handleDraw() {
      // this.drawIndex = 0;
      this.pointsArr = [];
      let pointsGlArr = []; // new BMapGL.Point

      this.bdMap.removeEventListener("click", () => {});
      this.mapClickListener = this.bdMap.addEventListener("click", (e) => {
        this.pointsArr.push({ lat: e.latlng.lat, lng: e.latlng.lng });
        pointsGlArr = this.pointsArr.map(
          (item) => new BMapGL.Point(item.lng, item.lat)
        );
        this.makePoint(
          { lng: e.latlng.lng, lat: e.latlng.lat },
          this.getIcon({
            index: this.drawIndex,
            length: this.pointsArr.length,
          }),
          { name: "handlePoint" + this.drawIndex },
          false,
          false
        );
        // 
        this.polyline = new BMapGL.Polyline(pointsGlArr, {
          strokeStyle: "dashed",
          strokeColor: "blue",
          strokeTexture: {
            url: this.img.Icon_road_blue_arrow,
            // url: "https://mapopen-pub-jsapigl.bj.bcebos.com/svgmodel/Icon_road_blue_arrow.png",
            width: 16,
            height: 64,
          },
          strokeWeight: 8,
          strokeOpacity: 0.8,
          node: this.node,
        });
        this.bdMap.addOverlay(this.polyline); // 
        this.drivingDistance = this.convertDistance(
          this.polyline.getLength() + ""
        );
        this.polyline.enableEditing(); // 
        this.drawIndex++;

        if (this.drawIndex > 1) {
          this.bdMap.removeEventListener("click", this.mapClickListener);
          this.polyline.addEventListener("editend", (e) => {
            // this.pointsArr = this.polyline.getPath() // 
            let pointsArr = this.polyline.getPath();
            // 
            this.removeMarker("handlePoint0");
            this.removeMarker("handlePoint1");
            this.makePoint(
              pointsArr[0],
              this.getIcon({
                index: 0,
                length: 0,
              }),
              { name: "handlePoint0" },
              false,
              false
            );
            this.makePoint(
              pointsArr[pointsArr.length - 1],
              this.getIcon({
                index: 1,
                length: 2,
              }),
              { name: "handlePoint1" },
              false,
              false
            );
          });
          // 
          this.polyline.addEventListener("lineupdate", (e) => {
            this.pointsArr = [];
            this.pointsArr = JSON.parse(
              JSON.stringify(this.polyline.getPath())
            );
            console.log(this.pointsArr);
          });
        }
      });
    },

    /**
     * @Event  (,)
     * @description:
     * @author: mhf
     * @time: 2024-02-21 14:21:40
     **/
    getIcon(params) {
      let {
        index,
        length,
        width = 25,
        height = 40,
        offsetW = 0,
        offsetH = 0,
      } = params;
      // return new BMapGL.Icon(
      //     `../../assets/img/${index === 0 ? "startPoint" : index === length - 1 ? "endPoint" : "handlePoint"}.png`,
      //     new BMapGL.Size(width, height),
      //     {
      //       anchor: new BMapGL.Size(width / 2, height + 2), // []
      //       imageOffset: new BMapGL.Size(offsetW, offsetH),
      //     }, // 
      // );
      return new BMapGL.Icon(
        index === 0
          ? this.img.startPoint
          : index === length - 1
          ? this.img.endPoint
          : this.img.handlePoint,
        new BMapGL.Size(width, height),
        {
          anchor: new BMapGL.Size(width / 2, height + 2), // []
          imageOffset: new BMapGL.Size(offsetW, offsetH),
        } // 
      );
    },

    /**
     * @Event 
     * @description:
     * @author: mhf
     * @time: 2024-02-01 19:36:52
     **/
    removeMarker(name = "startPoint") {
      let markerArr = this.bdMap.getOverlays();
      for (let i = 0; i < markerArr.length; i++) {
        if (markerArr[i].customObj?.name === name) {
          this.bdMap.removeOverlay(markerArr[i]); // 
        }
      }
      // this.bdMap.clearOverlays() // 
    },

    /**
     * @Event 
     * @description:
     * @author: mhf
     * @time: 2024-02-19 18:09:56
     **/
    resetLine() {
      this.bdMap.clearOverlays(); // 
      this.transit?.clearResults(); // 
      this.pointsArr = [];
      this.drivingDistance = 0;
      this.drawIndex = 0;
      this.bdMap.removeEventListener("click", this.mapClickListener);
      this.mapClickListener = null;
      this.handleDraw();
    },

    /**
     * @Event 
     * @description:
     * @author: mhf
     * @time: 2024-02-21 15:56:42
     **/
    searchPlace(e) {
      this.searchRes = [];
      if (e) {
        const myFun = () => {
          this.searchRes = local.getResults()._pois;
        };
        var local = new BMapGL.LocalSearch(this.bdMap, {
          onSearchComplete: myFun,
        });
        local.search(e);
      } else {
        this.removeMarker("searchPoint");
      }
    },
    searchCenter(e) {
      if (this.lng && this.lat) {
        if (this.centerTimer) {
          clearTimeout(this.centerTimer);
        }
        this.centerTimer = setTimeout(() => {
          this.bdMap.centerAndZoom(
            new BMapGL.Point(this.lng, this.lat),
            this.mapZoom
          );
        }, 500);
      }
    },

    chooseIt(item) {
      this.searchPoint = item.point;
      if (this.searchPoint) {
        this.removeMarker("searchPoint");
      }
      this.makePoint(
        { lng: this.searchPoint.lng, lat: this.searchPoint.lat },
        null,
        { name: "searchPoint" },
        false
      );
      this.bdMap.centerAndZoom(item.point, 18);
    },

    flat(arr) {
      return arr.reduce((prev, curr) => {
        return prev.concat(Array.isArray(curr) ? this.flat(curr) : curr);
      }, []);
    },

    /**
     * 
     * @param: pointsArr, config
     * @return:
     * @author: mhf
     * @time: 2024-07-16 18:43:56
     **/
    drawPolyLines(pointsArr, config, obj = {}) {
      let pointsGlArr = []; // new BMapGL.Point
      const getPointGlArr = () => {
        return pointsArr.map((item) => new BMapGL.Point(item.lng, item.lat));
      }; // GL
      pointsGlArr = getPointGlArr();
      console.log(pointsGlArr, "pointsGlArr");
      let polyline = new BMapGL.Polyline(pointsGlArr, config);
      polyline.customObj = obj;
      this.bdMap.addOverlay(polyline);
    },

    getAllRoads() {
      // roadAllList().then((res) => {
      //   if (res.code === 1) {
      //     this.allRoads = res.data;
      //     if (this.showRoads) {
      //       this.drawArrRoads();
      //     }
      //   }
      // });
    },

    drawArrRoads() {
      let obj = {
        type: "draw-all-roads",
      };
      let config = {
        strokeColor: "#1492ff",
        strokeWeight: 5,
        strokeOpacity: 1,
        strokeStyle: "solid",
        zIndex: 1,
      };
      this.allRoads.forEach((item) => {
        this.drawPolyLines(JSON.parse(item.roadCoordinate), config, obj);
      });
    },

    removeAllRoads() {
      let overlaysArr = this.bdMap.getOverlays();
      const overlaysToRemove = overlaysArr.filter((overlay) => {
        return overlay.customObj?.type === "draw-all-roads";
      });
      /*  */
      if (overlaysToRemove.length > 0) {
        overlaysToRemove.forEach((overlay) => {
          this.bdMap.removeOverlay(overlay);
        });
      }
    },

    handleChange(e) {
      console.log(e, "E");
      if (!e) {
        this.removeAllRoads();
      } else {
        this.drawArrRoads();
      }
    },
  },
  created() {},
  mounted() {},
};
</script>

<style lang="scss" scoped>
::v-deep .el-dialog__body {
  padding: 20px 0 0 !important;
}

.point-self {
  position: absolute;
  z-index: 999;
  background-color: #fff;
  left: 40px;
  border-radius: 4px;
  overflow: hidden;
  top: 60px;
  // transition: width 1s;
  &-top {
    background-color: #1492ff;
    height: 40px;
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    color: #fff;

    .top-title {
      font-weight: bold;
    }

    // .top-icon {
    //   color: #fff !important;
    // }
  }

  &-hidden {
    padding: 10px;
    cursor: pointer;
    // line-height: 20px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  }
}

.dialog-body {
